import React from "react";
import T from 'i18n-react'
import { Link } from "react-router-dom";
import {
  Hero,
  HeroBody,
  HeroFooter,
  Container,
  Box,
  Columns,
  Column,
  Image,
  Field,
  Button
} from "bloomer";
import FloatLabelnput from "../components/FloatLabelnput";
import FloatLabelPassword from "../components/FloatLabelPassword";
import Footer from "../components/Footer";

import recaptchaconf from "../config/recaptcha.json";

import globalStorage from "react-global-storage";
import alertify from "alertify.js";
import ReCaptcha from "react-google-recaptcha";

import "../assets/styles/page-login.css";

import logo from "../assets/images/SVG/capitual-logo.svg";

import cap from "../lib/capsdk";
import { requestPushToken } from '../lib/push'

import { detectLanguage } from '../helper/i18n'

class Login extends React.Component {
  state = {
    isLoading: false,
    email: "",
    password: "",
    otp_token: "",
    keep: false,
    requestedToken: false,
    isUsernameError: false,
    isPasswordError: false,
    isTokenError: false,
    caps: false
  };

  componentDidMount = () => {
    // clear cache
    cap.clear();

    let qs = new URLSearchParams(window.location.search);

    if (cap.sess_key)
      this.props.history.push(qs.get("continue") || "/dashboard")

    else if (!qs.has("continue"))
      this.props.history.push("/login?continue=%2Fdashboard");
  };

  blurEmail = () => {
    cap.requestA('members.resendSignupEmail', { email: this.state.email, locale: detectLanguage() })
      .then(() => {
        alertify.okBtn("OK").alert(T.translate("pages.login.noPassword"))
      })
      .catch(() => {})
  }

  doLogin = async e => {
    if (e) e.preventDefault();
    this.setState({ isLoading: true });

    try {
      let sess_key = await cap.requestA("members.login", {
        locale: detectLanguage(),
        login: this.state.email,
        password: this.state.password,
        otp_token: this.state.otp_token ? this.state.otp_token.replace(/[^0-9]/g, "") : null
      });
      this.setState({ isLoading: false });

      localStorage.removeItem("sess_key");
      sessionStorage.removeItem("sess_key");

      if (this.state.keep)
        localStorage.setItem("sess_key", sess_key);
      else
        sessionStorage.setItem("sess_key", sess_key);

      cap.requestA('members.profileset', {args: { locale: detectLanguage() }})

      // push
      if (this.state.keep) {
        requestPushToken(function(token) {
          if (typeof token === 'string') {
            cap.requestA('push.addToken', {
              type: 'desktop',
              token
            })
          }
        })
      }

      let qs = new URLSearchParams(window.location.search);

      if (qs.has("continue") && qs.get("continue").startsWith("/"))
        this.props.history.push(qs.get("continue"));
      else this.props.history.push("/dashboard");
    } catch (e) {
      this.setState({ isLoading: false });
      switch (e) {
        case "InvalidUsername":
          this.setState({ isUsernameError: true });
          break;

        case "InvalidPassword":
          this.setState({ isPasswordError: true, password: "" });
          break;

        case "InvalidOTPToken":
          this.setState({ isTokenError: true });
          break;

        case "InvalidEmailToken":
          this.setState({ isTokenError: true });
          break;

        case "RequestedEmailToken":
          this.setState({
            requestedToken: "email",
            isUsernameError: false,
            isPasswordError: false
          });
          break;

        case "RequestedOTPToken":
          this.setState({
            requestedToken: "otp",
            isUsernameError: false,
            isPasswordError: false
          });
          break;

        case "UserIsDisabled":
          alertify.okBtn("Close").alert(
            T.translate("login.lockedAccount")
          );
          break;
      }
    }
  };

  onChange = e => {
    //e.preventDefault();
    let { name, value, type } = e.target;

    let caps = e.getModifierState && e.getModifierState("CapsLock");

    if (type == "checkbox")
      this.setState({ [name]: !this.state[name] });
    else
      this.setState({ [name]: value, caps }, () => {
      if (name==='otp_token' && value.length === 6)
        this.doLogin()
    });
  };

  render() {
    return (
      <Hero className="page-login" isFullHeight>
        <div className="recaptcha-badge">
          <ReCaptcha
            sitekey={recaptchaconf.sitekey}
            onChange={val => this.setState({ recaptcha_result: val })}
            size="invisible"
            badge="inline"
          />
        </div>
        <HeroBody>
          <Container>
            <Columns isCentered>
              <Column isSize={4} isMarginless isPaddingless>
                <form onSubmit={this.doLogin}>
                  <Box>
                    <Columns isMultiline>
                      <Column isSize="auto">
                        <Link to="/">
                          <Image
                            src={logo}
                            isSize="64x64"
                            hasTextAlign="centered"
                            style={{ margin: "0 auto" }}
                          />
                        </Link>
                      </Column>
                      <br />

                      {!this.state.requestedToken && (
                        <React.Fragment>
                          <Column isSize={12}>
                            <FloatLabelnput
                              label={T.translate("pages.login.usernameInput")}
                              name="email"
                              type="email"
                              value={this.state.email}
                              onChange={this.onChange}
                              onBlur={this.blurEmail}
                              isColor={
                                this.state.isUsernameError
                                  ? "danger"
                                  : undefined
                              }
                              helpText={
                                this.state.isUsernameError &&
                                T.translate("pages.login.invalidUsername")
                              }
                              required
                              autoFocus
                            />
                          </Column>
                          <Column isSize={12}>
                            <FloatLabelPassword
                              label={ T.translate("pages.login.passwordInput") }
                              type="password"
                              name="password"
                              value={this.state.password}
                              onChange={this.onChange}
                              isColor={
                                this.state.isPasswordError
                                  ? "danger"
                                  : undefined
                              }
                              helpText={
                                this.state.isPasswordError &&
                                T.translate("pages.login.invalidPassword")
                              }
                              required
                            />
                          </Column>
                        </React.Fragment>
                      )}

                      {this.state.requestedToken == "otp" && (
                        <Column isSize={12}>
                          <FloatLabelnput
                            label={T.translate("pages.login.otp")}
                            type="password"
                            name="otp_token"
                            pattern="[0-9]*"
                            inputmode="numeric"
                            value={this.state.otp_token}
                            onChange={this.onChange}
                            isColor={
                              this.state.isTokenError ? "danger" : undefined
                            }
                            helpText={
                              this.state.isTokenError && T.translate("pages.login.otpError")
                            }
                            required
                            autoFocus
                          />
                        </Column>
                      )}

                      {this.state.requestedToken == "email" && (
                        <Column isSize={12}>
                          <FloatLabelnput
                            label={T.translate("pages.login.emailToken")}
                            type="password"
                            name="otp_token"
                            value={this.state.otp_token}
                            onChange={this.onChange}
                            isColor={
                              this.state.isTokenError ? "danger" : undefined
                            }
                            helpText={
                              this.state.isTokenError
                                ? T.translate("pages.login.emailTokenInvalid")
                                : T.translate("pages.login.emailTokenHelper")
                            }
                            required
                            autoFocus
                          />
                        </Column>
                      )}

                      {!this.state.requestedToken && (
                        <Column isSize={12}>
                          <Columns isGapless isMarginless>
                            <Column isPulled="left">
                              <Field class="field">
                                <input
                                  className="is-checkradio is-circle is-info"
                                  name="keep"
                                  checked={this.state.keep}
                                  onChange={this.onChange}
                                  id="remember-me"
                                  type="checkbox"
                                />
                                <label for="remember-me">{ T.translate("pages.login.rememberMe") }</label>
                              </Field>
                            </Column>
                            <Column isPulled="right" hasTextAlign="right" className='is-hidden-mobile'>
                              <Link to="/recover">{ T.translate("pages.login.forgotPwd") }</Link>
                            </Column>
                          </Columns>
                        </Column>
                      )}

                      <Column isSize={12}>
                        <br className='is-hidden-tablet' />
                        <Button
                          isColor="primary"
                          isSize="large"
                          isFullWidth
                          isLoading={this.state.isLoading}
                          type="submit"
                        >
                          { T.translate("pages.login.loginBtn") }
                        </Button>

                        { this.state.requestedToken &&
                          <p className="has-text-centered">
                            <br/>
                            <a href="#" onClick={() => this.setState({ requestedToken: false, email: "", password: "" })}>{ T.translate("pages.login.goBack") }</a>
                          </p>
                        }
                      </Column>
                      {!this.state.requestedToken && (
                        <Column>
                          <p className="has-text-centered">
                            { T.translate("pages.login.notRegistered") }{" "}
                            <Link to="/signup">{ T.translate("pages.login.signupLink") }</Link>
                          </p>
                          <br className='is-hidden-tablet' />
                          <p className='has-text-centered is-hidden-tablet'>
                            <Link to="/recover">{ T.translate("pages.login.forgotPwd") }</Link>
                          </p>
                        </Column>
                      )}
                    </Columns>
                  </Box>
                </form>
              </Column>
            </Columns>
          </Container>
        </HeroBody>
        <HeroFooter>
          <Footer />
        </HeroFooter>
      </Hero>
    );
  }
}

export default Login;



